<template>
	<div id="cashier">
		<commonHeader :title="'收银台'"></commonHeader>
		<div class="cell bg-fff bor-none mb-20">
			订单金额
			<span class="fl-right" style="color: #fe4436;">￥{{amount}}</span>
		</div>

		<div class="cell bg-fff">选择付款方式</div>
		<div class="cell bg-fff" style="height: 1.87rem;" @click="payment(1)">
			<div class="weixin">
				<span class="pay-type">微信支付</span>
				<span class="pay-remind">推荐使用</span>
				<img src="../../assets/arrow_gray.png" class="arrow_gray">
			</div>

		</div>
		<div class="cell bg-fff" style="height: 1.87rem;" @click="payment(2)">
			<div class="wallet">
				<span class="pay-type">我的钱包</span>
				<span class="pay-remind">使用钱包余额支付</span>
				<img src="../../assets/arrow_gray.png" class="arrow_gray">
			</div>
		</div>

		<div class="shadow" v-show="showPaw">
			<div class="psw-wrap">

				<div class="title">
					<img class="lt-arrow" src="../../assets/shan-right.png" @click="hidden" /> 请输入支付密码
				</div>
				<div class="content">
					<div class="psw-input">
						<input type="password" name="" placeholder="支付密码" v-model="paypsw" /><span class="payment-btn active" @click="getPaySubmit(3)">付款</span>
					</div>
					<!--<div class="forget" @click="forgetPsw">忘记密码？</div>-->
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import md5 from 'blueimp-md5';

	export default {
		data() {
			return {
				amount: this.$route.query.realAmount,
				showPaw: false,
				paypsw: '',
				wxPayObj: '',
				isPaypw:'',
			}
		},
		watch: {},
		created() {
//			this.$wechat.wxInit(this)
				this.getUserDetails()
		},
		methods: {
			hidden() {
				this.showPaw = false
			},
			forgetPsw() {
				this.$router.push({
					name: 'EditPay'
				})
			},
			showRemind() {
				this.$popup({
					title: '',
					content: ''
				}).then((res) => {
					console.log(res)
				})
			},
			payment(opt) {
				if(opt == 1) {
					this.getPaySubmit(11)
					//微信支付
				} else if(opt == 2) {
					if(this.isPaypw == 1){
						this.showPaw = true
					}else{
						this.$popup({
							title: '设置密码',
							content: '请先设置支付密码'
						}).then((res) => {
							console.log(res)
							if(res == 'sure'){
								this.$router.push({name:'EditPay',query:{type:1}})
							}
						})
					}
				}
			},
			getUserDetails() {
				this.$Api.UserDetails(0).then((res) => {
					console.log(res);
					if(res.q.s == 0){
						this.isPaypw = res.q.user.isPaypw
					}
				})
			},
			getPaySubmit(opt) {
				let params = {}
				if(opt == 3) {
					if(this.paypsw.length > 0) {
						params = {
							a: 0,
							payment: opt,
							amount: this.amount,
							orderId: this.$route.query.orderId,
							psw: md5(this.$paykey + md5(this.paypsw))
						}
					} else {
						this.$toast('请输入密码')
						return
					}
				} else if(opt == 11) {
					params = {
						a: 0,
						payment: opt,
						amount: this.amount,
						orderId: this.$route.query.orderId,
					}
				}
				this.$Api.PaySubmit(params).then((res) => {
					console.log(res)
					this.paypsw = ''
					if(res.q.s == 0) {
						if(opt == 3) {
							this.$router.replace({name: 'PaySuccess'})
						} else if(opt == 11) {
							this.wxPayObj = JSON.parse( res.q.wxPay )
							this.onBridgeReady()
						}
					}else {
						this.$toast(res.q.d)
					}
				})
			},
			onBridgeReady() {
			let self = this
			console.log(this.wxPayObj)
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					"appId": this.wxPayObj.appId, //公众号名称，由商户传入     
					"timeStamp": this.wxPayObj.timeStamp, //时间戳，自1970年以来的秒数     
					"nonceStr": this.wxPayObj.nonceStr, //随机串     
					"package": this.wxPayObj.package,
					"signType": this.wxPayObj.signType, //微信签名方式：     
					"paySign":  this.wxPayObj.paySign//微信签名 
				},
				function(res) {
					console.log(res)
					if(res.err_msg == "get_brand_wcpay_request:ok") {
						self.$router.replace({name:'PaySuccess'})
					} // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
				}
			);
		},
//			if(typeof WeixinJSBridge == "undefined") {
//				if(document.addEventListener) {
//					document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
//				} else if(document.attachEvent) {
//					document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
//					document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
//				}
//			} else {
//				this.onBridgeReady();
//			},

			wxPay() {
				wx.chooseWXPay({
					timestamp: this.wxPayObj.timestamp,
					nonceStr: this.wxPayObj.nonceStr,
					package: this.wxPayObj.package,
					signType: this.wxPayObj.signType,
					paySign: this.wxPayObj.paySign,
					success: function(res) {
						// 支付成功后的回调函数
						console.log(res);
						//        this.isPay = true;
						//        //alert(res.errMsg);
						//        if(res.errMsg == "chooseWXPay:ok" ) {
						//          console.log('支付成功');
						//          setTimeout(function(){
						//            location.hash = '/userDetail';
						//          },500);
						//
						//        }
					},
					error: function(res) {
						this.isPay = true;
//						alert(res);
					}
				});
			},
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#cashier {
		min-height: 100%;
		background: #f0f0f0;
		.bor-none {
			border: none;
		}
		.mb-20 {
			margin-bottom: 0.27rem;
		}
		.weixin,
		.wallet {
			height: 1.87rem;
			width: 100%;
			line-height: 0.7rem;
			padding-top: 0.21rem;
			padding-left: 0.88rem;
			span {
				display: block;
			}
			.pay-type {
				font-size: 0.4rem;
				color: #333;
			}
			.pay-remind {
				font-size: 0.37rem;
				color: #999;
			}
		}
		.weixin {
			background: url("../../assets/cart/icon_wechat.png") no-repeat center left;
			background-size: 0.59rem auto;
		}
		.wallet {
			background: url("../../assets/cart/ico_wallet.png") no-repeat center left;
			background-size: 0.59rem auto;
		}
		.shadow {
			.psw-wrap {
				position: absolute;
				right: 0;
				bottom: 0;
				left: 0;
				background: #fff;
				.title {
					position: relative;
					height: 1.2rem;
					line-height: 1.2rem;
					font-size: 0.48rem;
					color: #000;
					text-align: center;
					border-bottom: 1px solid #ccc;
					.lt-arrow {
						@include lt-arrow;
						width: 0.24rem;
						height: 0.45rem;
					}
				}
				.content {
					padding: 0.79rem 0.32rem 1.4rem 0.32rem;
					overflow: hidden;
				}
				.psw-input {
					border: 1px solid #999999;
					border-radius: 4px;
					height: 1.15rem;
					position: relative;
					>input {
						border: none;
						width: 100%;
						height: 100%;
						padding: 0 0.2rem;
						margin-bottom: 0;
						line-height: 1.15rem;
						font-size: 0.43rem;
					}
					.payment-btn {
						position: absolute;
						right: 0;
						top: 0;
						border-radius: 0 4px 4px 0;
						height: 1.1rem;
						line-height: 1.15rem;
						font-size: 0.43rem;
						width: 1.87rem;
						text-align: center;
						background-color: #d9d9d9;
						color: #999999;
					}
					.payment-btn.active {
						color: #fff;
						@include bg-color;
					}
				}
				.forget {
					font-size: 0.43rem;
					float: right;
					margin-top: 0.4rem;
				}
			}
		}
	}
</style>


